.color--grey {
    color: #5a5a5a;
  }
  
  .container {
    align-items: center;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: center;
    min-height: 200px;
    padding: 1rem;
  }
  
  .font-size--sm {
    font-size: 0.75rem;
  }
  
  .icon--lg {
    line-height: 0.8;
    font-size: 1.5rem;
  }
  
  .margin--xs {
    margin: 0.25rem;
  }
  
  .margin-bottom--sm {
    margin-bottom: 0.5rem;
  }
  
  .margin-top--md {
    margin-top: 1rem;
  }
  
  .margin-top--sm {
    margin-top: 0.5rem;
  }
  
  .margin-right--xs {
    margin-right: 0.25rem;
  }
  